.product {
  &-container {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 15px; }
  &-panel {
    background-color: #fff;
    box-shadow: 0px 0px 4px -1px rgba(0, 0, 0, 0.75);
    margin: 25px 15px 0px 15px;
    .shop {
      margin-top: 15px;
      &-wrap {
        justify-content: space-around; }
      &__item:hover {
        box-shadow: 0px 0px 12px 0px rgba(170,155,119,0.75);
        cursor: pointer; }
      &__item {
        box-shadow: 0px 0px 12px -8px rgba(0,0,0,0.9);
        border: none;
        flex: 1; } } } }
.page-item {
  flex: 1; }

